import { useRouteMatch } from "react-router-dom";
import { useStores } from "../store";
import { useCallback } from "react";
import { TcfVendorConfigurationModel } from "../models";

const useRouteTcfVendorConfiguration = () => {
  const {
    params
  } = useRouteMatch();
  const {
    tcfStore
  } = useStores(); // Get blocker or return draft object

  const vendorConfigurationId = +params.vendorConfiguration;
  const id = isNaN(+vendorConfigurationId) ? 0 : +vendorConfigurationId;
  const queried = !!vendorConfigurationId;
  const vendorConfiguration = tcfStore.vendorConfigurations.entries.get(id) || new TcfVendorConfigurationModel(tcfStore.vendorConfigurations, {
    id: 0
  });
  const link = "#/cookies/tcf-vendors";
  const editLink = useCallback(({
    key
  }) => "#/cookies/tcf-vendors/edit/".concat(key), [vendorConfiguration]);
  const addLink = "#/cookies/tcf-vendors/new";
  return {
    vendorConfiguration,
    id,
    queried,
    fetched: vendorConfiguration.key !== 0,
    link,
    editLink,
    addLink
  };
};

export { useRouteTcfVendorConfiguration };